export default {
	data() {
		return {
			title: 'Hello',
			orderNo:'',
			trolley:null,
			address:null,
			amount:0
		}
	},
	onShow(){
		this.getAddress();
	},
	onLoad(option) {
		this.getAddress();
		this.getTrolley();
	},
	methods: {
		shop(){
			uni.reLaunch({
				"url":"/pages/shop/shop"
			})
		},
		changeAddress(){
			uni.navigateTo({
				"url":"/pages/mine/address?bill=true"
			})
		},
		pay(){
			let _this = this;
			this.$ajax("/order/buy","POST",{addressId:_this.address.id}).then(res=>{
				if(res.code == 200){
					uni.requestPayment({
						provider: "wxpay",
						timeStamp:res.paymentResult.payment.timeStamp,
						nonceStr:res.paymentResult.payment.nonceStr,
						package:res.paymentResult.payment.package,
						signType:res.paymentResult.payment.signType,
						paySign:res.paymentResult.payment.paySign,
						success:function(){
							// uni.setStorageSync('qrcodeNo',res.no);
							uni.navigateTo({
								"url":"/pages/mine/billdetail/?orderNo="+res.paymentResult.no
							})
							
						},
						fail:function(){
							uni.showToast({
								"title":"取消支付,将放入待支付队列",
								"icon":"none"
							})
							setTimeout(function(){
								uni.navigateTo({
									"url":"/pages/mine/billdetail/?orderNo="+res.paymentResult.no
								})
							},999)
						},
					})
				}else{
					uni.showToast({
						"title":res.msg,
						"icon":"none"
					})
				}
				
			})
		},
		getAddress(){
			let _this = this;
			this.$ajax("/useraddress/page","POST",{pageNo:1,pageSize:1,orderName:"default_flag",orderType:"desc"}).then((res)=>{
				
				let str = "";
				if(res.list.length == 0){
					uni.showModal({
						"title":"设置地址",
						"content":"你还没有设置地址，请先设置默认地址",
						"success":function(resModal1){
							if(resModal1.confirm){
								uni.navigateTo({
									"url":"/pages/mine/address?bill=true"
								})
							}
						}
					})
					return false;
				}else{
					_this.address = res.list[0];
				}
			})
		},
		toUrl(url){
			uni.navigateTo({
				"url":url
			})
		},
		getTrolley(){
			let _this = this;
			this.$ajax("/order/checkOut","POST",{}).then(res=>{
				_this.trolley = res.cart.cartItems;
				_this.amount = res.amount
				// console.log(res);
			})
		}
		// detail(){
		// 	let _this = this;
		// 	this.$ajax("/order/detail","POST",{orderNo:_this.orderNo}).then((res)=>{
		// 		_this.billDetail = res
		// 	})
		// }
	},
	filters:{
		position(value){
			if(value == -1){
				return "取消订单";
			}
			if(value == 1){
				return "下单";
			}
			if(value == 2){
				return "已支付";
			}
			if(value == 3){
				return "已发货";
			}
			if(value == 4){
				return "已收货";
			}
			if(value == 0){
				return "交易成功";
			}
		},
		size(value){
			let sizeArr = JSON.parse(value);
			let size = "";
			sizeArr.map((res)=>{
				size = size + " " + res.value 
			})
			return size;
		},
		dateTime(value){
			let dateArr = [];
			dateArr = value.split(".");
			let dateTime = dateArr[0].replace("T"," ");
			return dateTime;
		}
	}
}
